import React , {Component} from 'react';
import './css.css';
import $ from 'jquery';
import { Redirect } from 'react-router-dom';

import {setLogin, LoginUser, isLogin} from "./index";

class Login extends Component {
    constructor() {
        super();
        this.state = {redirect: false, word1: '', word2: ''};
        this.handleWord1Change = this.handleWord1Change.bind(this);
        this.handleWord2Change = this.handleWord2Change.bind(this);
    }
    ladder = "";
    handleWord1Change(e) {
        this.setState({word1:e.target.value});
    }
    handleWord2Change(e) {
        this.setState({word2:e.target.value});
    }
    handleConfirmClick = () => {
        if(!isLogin) {
            alert("Please login first!");
            this.setState({redirect: true});
        }
        else {
            $.ajax({
                url: "/find",
                data: {
                    word1: this.state.word1,
                    word2: this.state.word2
                },
                context: document.body,
                async: false,
                type: "get",
                success: function (data) {
                    this.ladder = data.toString();
                }
            });
            window.location.reload();
        }
    };

    render() {
        let islogin = false;
        $.ajax({
            url:"/checkstate",
            context:document.body,
            async:false,
            type:"get",
            success: function(data) {
                console.log(data);
                if(data.toString() !== "false") {
                    islogin = true;
                }
            }
        });
        if(islogin) {
            setLogin(true, LoginUser);
        }
        else {
            setLogin(false, "");
        }

        if(this.state.redirect) {
            return(
            <Redirect push to="/login"/>);
        }
        return (
            <div className="LoginFun">
                <div>
                    <div>Word1:</div>
                    <input type="text" value={this.state.word1} onChange={this.handleWord1Change}/>
                </div>
                <div>
                    <div>Word2:</div>
                    <input type="text" value={this.state.word2} onChange={this.handleWord2Change}/>
                </div>
                <div>
                    <button onClick={this.handleConfirmClick} className="Button1">Confirm</button>
                </div>
                <div>
                    <div>Ladder:</div>
                    <div>{this.ladder}</div>
                </div>
            </div>
        );
    }
}

export default Login;


