import ReactDOM from 'react-dom';
import React, {Component} from 'react';
import { BrowserRouter as Router, Route, Link} from 'react-router-dom'
import './css.css';
import $ from 'jquery';

import login from './login'
import wordladder from './wordladder'

import registerServiceWorker from './registerServiceWorker';

let style = {
    backgroundColor: '#8dc63f',
    fontSize: 20,
    fontWeight: 500,
    height: 52,
    padding: '0 3vmin',
    borderRadius: 5,
    color: '#fff'
};

let isLogin = false;
let LoginUser = "";
let setLogin = function (value, username) {
    isLogin = value;
    if(isLogin)
        LoginUser = username;
};
class LoginButton extends Component {
    handleClick = () => {
        $.ajax({
            url: "/userlogout",
            context: document.body,
            async: false,
            type: "get"
        });
        setLogin(false, null);
        window.location.reload();
    };
    render() {
        return (
            isLogin?
                <div>
                    <button onClick={this.handleClick} style={style}>Logout</button>
                </div>
                :
                <div className="Link3">
                    <Link to="/login" style={style}>Login</Link>
                </div>
        );
    }
}

class Wordladder extends Component {
    render() {
        let islogin = false;
        $.ajax({
            url:"/checkstate",
            context:document.body,
            async:false,
            type:"get",
            success: function(data) {
                if(data.toString() !== "false") {
                    islogin = true;
                }
            }
        });
        if(islogin) {
            setLogin(true, LoginUser);
        }
        else {
            setLogin(false, "");
        }
        return (
            <Router>
                <div>
                    <div>
                        <h1 className="title">WordLadder</h1>
                        <LoginButton/>
                    </div>
                    <hr/>
                    <Route  exact path="/" component={wordladder}/>
                    <Route path="/login" component={login}/>
                </div>
            </Router>
        );
    }
}

ReactDOM.render(<Wordladder/>, document.getElementById('root'));
registerServiceWorker();

export {setLogin, isLogin, LoginUser}



